import React, { useState } from 'react';
import { Stage, Layer, Text, Line, Rect, Group } from 'react-konva';
import './App.css';

function App() {
  const [prompt, setPrompt] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [commands, setCommands] = useState([]);

  const handleGenerate = async () => {
    if (!prompt.trim()) return;
    
    console.log('Starting generation with prompt:', prompt);
    setLoading(true);
    setError('');
    setCommands([]);
    
    try {
      console.log('Making API request...');
      const response = await fetch('/api/generate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ prompt }),
      });

      console.log('Response status:', response.status);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log('Received data:', data);
      
      if (data.commands) {
        console.log('Found commands, simulating streaming...');
        // Simulate streaming by adding commands one by one
        for (let i = 0; i < data.commands.length; i++) {
          console.log(`Adding command ${i + 1}:`, data.commands[i]);
          await new Promise(resolve => setTimeout(resolve, 800)); // 800ms delay
          setCommands(prev => {
            const newCommands = [...prev, data.commands[i]];
            console.log('Updated commands:', newCommands);
            return newCommands;
          });
        }
      } else if (typeof data === 'string') {
        console.log('Received string data, converting to command');
        setCommands([{ type: 'text', text: data, x: 20, y: 20, fontSize: 16 }]);
      } else {
        console.log('Unexpected response format:', data);
        setError('Unexpected response format');
      }
    } catch (err) {
      console.error('Error in handleGenerate:', err);
      setError(`Error: ${err.message}`);
    } finally {
      console.log('Setting loading to false');
      setLoading(false);
    }
  };

  // Streaming response handler
  const fetchStreamingResponse = async (prompt) => {
    const response = await fetch('/api/gemini', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ prompt }),
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const reader = response.body.getReader();
    const decoder = new TextDecoder();
    let buffer = '';

    while (true) {
      const { done, value } = await reader.read();
      if (done) break;
      
      buffer += decoder.decode(value, { stream: true });

      // Process chunks line by line
      let parts = buffer.split('\n');
      buffer = parts.pop();
      
      for (const part of parts) {
        if (part.trim()) {
          try {
            if (part.startsWith('data: ')) {
              const data = part.substring(6);
              if (data === '[DONE]') break;
              
              const command = JSON.parse(data);
              // Add command with a small delay for visual effect
              await new Promise(resolve => setTimeout(resolve, 200));
              setCommands(prev => [...prev, command]);
            }
          } catch (e) {
            console.warn('Failed to parse streaming data:', part);
          }
        }
      }
    }
  };

  const clearCanvas = () => {
    setCommands([]);
    setError('');
  };

  const renderCommand = (command, index) => {
    const key = `${command.type}-${index}`;
    
    switch (command.type) {
      case 'text':
        return (
          <Text
            key={key}
            x={command.x || 20}
            y={command.y}
            text={command.text}
            fontSize={command.fontSize || 16}
            fill="#000"
            fontFamily="Arial"
            align="left"
          />
        );
      
      case 'equation':
        return (
          <Text
            key={key}
            x={command.x}
            y={command.y}
            text={command.latex || command.text}
            fontSize={command.fontSize || 18}
            fill="#000"
            fontFamily="Arial"
            fontStyle="italic"
          />
        );
      
      case 'line':
        return (
          <Line
            key={key}
            points={command.points}
            stroke="#000"
            strokeWidth={command.strokeWidth || 2}
            lineCap="round"
            lineJoin="round"
          />
        );
      
      case 'rect':
        return (
          <Rect
            key={key}
            x={command.x}
            y={command.y}
            width={command.width}
            height={command.height}
            stroke="#000"
            strokeWidth={2}
            fill="transparent"
          />
        );
      
      case 'group':
        return (
          <Group key={key} x={command.x} y={command.y}>
            {command.children && command.children.map((child, childIndex) => 
              renderCommand(child, `${index}-${childIndex}`)
            )}
          </Group>
        );
      
      default:
        return null;
    }
  };
  return (
    <div className="App">
      <header className="App-header">
        <h1>AI Teacher Whiteboard</h1>
        <p>Ask me to explain any concept and I'll draw it on the whiteboard!</p>
      </header>
      
      <div className="controls">
        <div className="input-group">
          <input
            type="text"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            placeholder="Ask me to explain something... (e.g., 'Explain Taylor series')"
            className="prompt-input"
            onKeyPress={(e) => e.key === 'Enter' && handleGenerate()}
          />
          <button 
            onClick={handleGenerate} 
            disabled={loading || !prompt.trim()}
            className="generate-btn"
          >
            {loading ? 'Generating...' : 'Generate'}
          </button>
          <button onClick={clearCanvas} className="clear-btn">
            Clear
          </button>
        </div>
      </div>

      {error && (
        <div className="error-message">
          {error}
        </div>
      )}

      <div className="whiteboard-container">
        <Stage 
          width={1200} 
          height={800} 
          draggable={true}
        >
          <Layer>
            {commands.map((command, index) => renderCommand(command, index))}
            {loading && commands.length > 0 && (
              <Text
                x={20}
                y={780}
                text="⏳ Generating more content..."
                fontSize={14}
                fill="#666"
                fontStyle="italic"
              />
            )}
          </Layer>
        </Stage>
        {commands.length === 0 && (
          <div style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            fontSize: '24px',
            color: '#666',
            pointerEvents: 'none'
          }}>
            {loading ? (
              <div style={{ textAlign: 'center' }}>
                <div>Generating content...</div>
                <div style={{ fontSize: '16px', marginTop: '10px', color: '#999' }}>
                  Drawing will appear here as it's generated
                </div>
              </div>
            ) : (
              'Enter a prompt and click Generate to see drawings here'
            )}
          </div>
        )}
      </div>

      <div className="info">
        <p>
          <strong>Examples:</strong> "Explain Taylor series", "Draw a neural network", 
          "Show the water cycle", "Explain photosynthesis"
        </p>
      </div>
    </div>
  );
}

export default App;
